            <span style="font-size: 1.2rem;color: #1989fa;">查看题目</span>
          </div>
        </li>
      </ul>
      <van-divider dashed>{{ recordList.length === 0 ? '暂无相关题目' : '已经到底了' }}</van-divider>
    </section>
  </div>
</template>

<script>
import {getMyRecordTest} from "@/api";
import {Dialog, Toast} from "vant";
import config from "@/config";

export default {
  name: "myRecord",
  data(){
    return{
      recordList: []
    }
  },
  mounted() {
    this.getMyRecordTest()
  },
  methods: {
    showTest(item,){
      // eslint-disable-next-line no-console
      console.log(item.testInfo.workBookId);
      this.$router.push(`/user/workbookDetail/${item.testInfo.workBookId}`)
    },
    async getMyRecordTest(){
      Toast.loading({
        duration: 0, // 持续展示 toast
        message: '加载中...',
        forbidClick: true,
        loadingType: 'spinner'
      });
      let data = await getMyRecordTest({
        userId: config.getCache('userId'),
      });
      Toast.clear();
      if (data.code === 200) {
        // eslint-disable-next-line no-console
        console.log(data.result.recordList);
        this.recordList = data.result.recordList;
      }else {
        Dialog.alert({
          title: '出错了',
          message: data.data,
        }).then(() => {
          this.$router.back();
        });
      }
    }
  }
}
</script>

<style scoped lang="stylus" ref="stylesheet/stylus">
>>> .van-nav-bar {
  z-index 2 !important
}
.zone-list {
  width: 100%
  padding: 1rem
  background-color: #fff

  > ul {
    width: 100%

    > li {
      width: 100%
      height: 4rem
      background-color: #f9f9f9
      margin-bottom: 1rem
      display flex
      align-items center
      padding: 0 1rem
      transition all .3s

      &.select {
        color #408ffb
      }

      > .left {
        height: 100%
        display flex
        align-items center

        > svg {
          width: 2rem
          height: 2rem
        }
      }

      > .middle {
        margin: 0 1rem
      }

      > .right {
        max-width 9rem
        min-width: 9rem;
        display flex
        height: 100%
        align-items: center;
        overflow: hidden;
        justify-content space-between
        color #848484

        svg {
          width: 1.8rem
          height: 1.8rem
        }
      }
    }
  }
}
</style>
